html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

a {
  text-decoration: none;
  color: inherit;
  display: inline-block; }

ul,
li {
  list-style: none;
  margin: 0;
  padding: 0; }

h1,
h2,
h3,
h4,
h5,
h6 {
  padding: 0;
  margin: 0; }

img {
  max-width: 100%;
  height: auto; }

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%; }

.content {
  flex: 1 0 auto; }

.footer {
  flex: 0 0 auto; }

body {
  font-family: 'Montserrat', sans-serif;
  font-weight: 500; }

.container {
  max-width: 1190px;
  margin: 0 auto;
  padding: 0 15px; }

.header {
  min-height: 650px;
  background: blue center center/cover no-repeat;
  position: relative;
  color: #F9FBFD; }
  .header__top {
    padding-top: 30px;
    padding-bottom: 210px;
    display: flex;
    align-items: center;
    justify-content: space-between; }
  .header__middle {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: 95px; }
  .header__title {
    font-weight: bold;
    font-size: 54px;
    margin-bottom: 30px; }
  .header__subtitle {
    font-weight: 400;
    font-size: 20px; }

.nav {
  color: #F9FBFD; }
  .nav__list {
    display: flex; }
  .nav__item:not(:last-child) {
    margin-right: 33px; }
  .nav__link {
    font-size: 16px; }

.footer {
  background: #011C40; }
  .footer__inner {
    display: flex;
    min-height: 138px;
    align-items: center;
    justify-content: space-between; }
  .footer__nav {
    display: flex;
    align-items: center; }
  .footer__logo {
    padding-right: 40px; }

.social__list {
  display: flex; }

.social__item:not(:last-child) {
  margin-right: 33px; }
